//page count 
let pageCount = 0;

//ÎèôÏûëÌï®Ïàò
function showNextPage() {
    if (pageCount === 0) {
        pageCheck01();

    } else if (pageCount === 1) {
        pageCheck02();
    }
    if (pageCount === 2) {
        prevPageBtn.style.display = 'none';
        nextPageBtn.style.display = 'none';
        loginPageInBtn.classList.add('login_page_in_btn_on');
        IdValueLoad();

        //Ï¥àÍ∏∞Ìôî
        for (let i = 0; i < necessaryInput.length; i++) {
            necessaryInput[i].value = '';
        }
    }
}

function showPrevPage() {
    if (pageCount === 0) {
        location.href = '../login_page/login.html';
    }
    if (pageCount === 1) {
        pageCount--;

        pageOn();

        //Ï¥àÍ∏∞Ìôî
        for (let i = 0; i < necessaryInput.length; i++) {
            necessaryInput[i].value = '';
        }
    }
}

function pageCheck01() {
    if (!(termsAllChkBtn.checked)) { //ÏïΩÍ¥ÄÎèôÏùò ÏïàÎêêÏùÑÍ≤ΩÏö∞
        cautionState = false;
        toggleCation();
    } else {
        pageCount++;

        //ÏïΩÍ¥Ä ÎèôÏùò Ï≤¥ÌÅ¨ 
        termsAllChkBtn.checked = false;
        termsAllChk();

        //Í≤ΩÍ≥† Î¨∏Íµ¨
        cautionState = true;
        toggleCation();

        pageOn();
    }
}

function pageCheck02() {
    let necessaryInputChk01 = Array.from(necessaryInput).every((event) => {
        return event.value;
    });
    let necessaryInputChk02 = Array.from(necessaryInput).every((event) => {
        return event.classList.contains('true_on');
    });
    //Î™®Îì† ÏõêÏÜåÍ∞Ä Ï°∞Í±¥ÏùÑ ÎßåÏ°±ÌïòÎ©¥ true, ÌïòÎÇòÎùºÎèÑ ÎßåÏ°±ÌïòÏßÄ ÏïäÏúºÎ©¥ falseÎ•º Î∞òÌôòÌïúÎã§.
    //Ìï¥Îãπ Î∞∞Ïó¥ÏïàÏóê ÏûàÎäî Í∞íÎì§Ïù¥ Ï°∞Í±¥ÏùÑ "Î™®Îëê" ÌÜµÍ≥ºÌï¥ÏïºÎßå trueÎ•º Î∞òÌôòÌï¥Ï§ÄÎã§.
    if (!(necessaryInputChk01 && necessaryInputChk02) ) {
        //necessaryInputChk, necessaryInputChk02 Í∞Ä falseÎùºÎäî ÎúªÏùÄ Ìï¥Îãπ Ï°∞Í±¥ ÌïòÎÇòÎèÑ ÎßåÏ°± Î™ªÌñàÎã§Îäî Îßê. 
        //pop_up Í¥ÄÎ†®
        popUpToggleBtn.classList.add('pop_up_on');
    } else {
        pageCount++;

        pageOn();
    }
}

function pageOn() {
    for (let i = 0; i < showPage.length; i++) {
        showPage[i].classList.remove('page_on');
        stepCircle[i].classList.remove('step_on');
        stepText[i].classList.remove('text_step_on');
    }

    showPage[pageCount].classList.add('page_on');
    stepCircle[pageCount].classList.add('step_on');
    stepText[pageCount].classList.add('text_step_on');

    if (pageCount === 1) {
        //ÌéòÏù¥ÏßÄ 1ÎêêÏùÑÎïå Ìè¨Ïª§Ïä§ Ìï†Îãπ 
        userName.focus();
    }
    //console.log(pageCount);
}

